import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React from 'react'
import Navbar from './components/Navbar/Navbar';
import Home from './components/Home/Home';
import Movie from './components/Movie/Movie';
import About from './components/About/About';
import Footer from './components/Footer/Footer';
import SignIn from './components/Login/login.jsx';
import SignUp from './components/SignUp/SignUp';
import EachMovie from './components/EachMovie/EachMovie';
import Search from './components/Search/Search.jsx'

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path='/HeeTV' exact >
            <Navbar menu='true' />
            <Home />
            <Movie />
            <About />
            <Footer />
          </Route>
          <Route path='/HeeTV/login'>
            <SignIn />
          </Route>
          <Route path='/HeeTV/signup'>
            <SignUp />
          </Route>
          <Route name="each" path='/HeeTV/each/'>
            <EachMovie />
          </Route>
          <Route path='/HeeTV/search/'>
            <Search />
          </Route>

        </Switch>
      </Router>
    </div>
  );
}

export default App;
