import React from 'react'
import Navbar from '../Navbar/Navbar';
import Footer from '../Footer/Footer'
import { useStateValue } from "../../redux/StateProvider";
import { useState, useEffect } from 'react'

function Search() {
    const [{ term }, dispatch] = useStateValue();
    const [movie, setMovie] = useState([]);
    console.log(term);
    const url = "https://www.omdbapi.com/?apikey=f5a376a&t=" + term


    //Fetch Quotes from API
    const getMovie = () => {
        fetch(url)
            .then((response) => response.json())
            .then((data) => setMovie(data));
    }
    console.log(movie)
    useEffect(() => {
        getMovie();
    }, []);

    return (
        <div>
            <Navbar menu='true' />
            <div style={{ minHeight: '300px', backgroundColor: 'black' }}>
                <div class="container" >
                    <div class="row" >
                        <div class="col-6">
                            <div class="card" style={{ marginTop: '30px', marginBottom: '30px' }}>
                                <img src={movie.Poster} class="card-img-top" alt="..." />
                                <div class="card-body">
                                    <h5 class="card-title">{movie.Title}</h5>
                                    <p class="card-text">{movie.Plot}</p>
                                    <p class="card-text">Released in : {movie.Year}</p>
                                    <p class="card-text">Genre : {movie.Genre}</p>
                                    <p class="card-text">Rating : {movie.imdbRating}/10</p>
                                    <a href="#" class="btn btn-primary">Watch Now</a>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            </div>
            <Footer />

        </div>

    )
}

export default Search
